/*------------------------------------|
|	Fonts                               |
|------------------------------------*/
@font-face {
  font-family: 'SD Georgia Light';
  src: url("../fonts/SDGeorgiaLight.ttf") format("truetype"), url("../fonts/SDGeorgiaLight.woff") format("woff");
  font-weight: 100;
  font-style: normal;
}
/*------------------------------------|
|	Base                                |
|------------------------------------*/
body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
  overflow: visible;
  color: #111;
  background-color: #fff;
  width: 100%;
  padding: 0;
  margin: 0;
}

body.is-menu-expanded {
  overflow: hidden;
}

img,
svg,
video {
  max-width: 100%;
  height: auto;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

p {
  font-size: 16px;
  font-size: 1rem;
  line-height: 20px;
  line-height: 1.25rem;
  letter-spacing: 0px;
  letter-spacing: 0rem;
  margin-bottom: 10px;
}

li {
  font-size: 16px;
  font-size: 1rem;
  line-height: 20px;
  line-height: 1.25rem;
  letter-spacing: 0px;
  letter-spacing: 0rem;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "SD Georgia Light", serif;
  font-weight: 100;
}

h2 {
  font-size: 31px;
  font-size: 1.9375rem;
  line-height: 23px;
  line-height: 1.4375rem;
  letter-spacing: -0.155px;
  letter-spacing: -0.00969rem;
  margin-bottom: 20px;
}

h3 {
  margin-bottom: 15px;
  font-size: 25px;
  font-size: 1.5625rem;
  line-height: 23px;
  line-height: 1.4375rem;
  letter-spacing: -0.125px;
  letter-spacing: -0.00781rem;
}

em {
  font-style: italic;
}

strong {
  font-weight: 700;
}

u {
  text-decoration: underline;
}

button,
input {
  padding: 5px;
}

input, select {
  width: 100%;
  border: none;
  padding: 15px 12px;
  border-radius: 5px;
  font-family: "SD Georgia Light", serif;
  font-weight: 100;
}

select {
  height: 100%;
}

button:focus, button:active, a:focus, a:active, input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active {
  outline: none;
}

table {
  width: 100%;
  border-collapse: collapse;
}

/*------------------------------------|
|	Animation                           |
|------------------------------------*/
/* Block nimations */
.animate {
  opacity: 0;
  transform: translateY(90px);
  transition: opacity 0.5s, ease transform 0.5s;
}
.animate.visible {
  transform: translateY(0);
  opacity: 1;
}

.animate-featured {
  opacity: 0;
  transform: translateX(90px);
  transition-property: transform, opacity;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  transition-delay: 0.5s;
}
.animate-featured.visible {
  transform: translateY(0);
  opacity: 1;
}

.animate-left {
  opacity: 0;
  transform: translateX(90px);
  transition-property: transform, opacity;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
@media (min-width: 1124px) {
  .animate-left {
    transform: translateX(0) translateY(50px);
  }
}
.animate-left.visible {
  transform: translateY(0);
  opacity: 1;
}

.animate-right {
  opacity: 0;
  transform: translateX(-90px);
  transition: opacity 0.5s, ease transform 0.5s;
}
@media (min-width: 1124px) {
  .animate-right {
    transform: translateX(0) translateY(50px);
  }
}
.animate-right.visible {
  transform: translateX(0);
  opacity: 1;
}
@media (min-width: 1124px) {
  .animate-right.visible {
    transform: translateX(0) translateY(0);
  }
}

.animate-opacity {
  opacity: 0;
  transition: ease opacity 0.6s;
}
.animate-opacity.visible {
  opacity: 1;
}

/*------------------------------------|
|	Utilities                           |
|------------------------------------*/
.clearfix:after:after {
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  content: " ";
}

