.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/* Slider */
.slick-next, .slick-prev {
  position: absolute;
  transform: translateY(-60%);
  width: 40px;
  height: 80px;
  font-size: 0;
  border: none;
  margin: auto;
  padding: 0;
  z-index: 10;
  top: 30%;
}
@media (min-width: 840px) {
  .slick-next, .slick-prev {
    top: 50%;
  }
}

.slick-prev {
  left: 0;
  background: url("../../assets/img/icons/arw-left-mob.png") no-repeat;
}
@media (min-width: 840px) {
  .slick-prev {
    left: -40px;
    background: url("../../assets/img/icons/arw-left.png") no-repeat;
  }
}

.slick-next {
  right: 0;
  background: url("../../assets/img/icons/arw-right-mob.png") no-repeat;
}
@media (min-width: 840px) {
  .slick-next {
    right: -40px;
    background: url("../../assets/img/icons/arw-right.png") no-repeat;
  }
}

.slick-dots {
  position: absolute;
  list-style-type: none;
  z-index: 10;
  left: 50%;
  transform: translate(-50%);
  bottom: 20px;
}
.slick-dots li {
  display: inline-block;
  margin: 0 10px 0 0;
}
.slick-dots li button {
  width: 15px;
  height: 15px;
  border: none;
  text-align: center;
  background-color: #969692;
  border-radius: 50%;
  font-size: 0;
}
.slick-dots .slick-active button {
  background-color: #d5d5d4;
  width: 20px;
  height: 20px;
}
@media (min-width: 600px) {
  .slick-dots {
    left: 105px;
    bottom: 80px;
  }
}
@media (min-width: 840px) {
  .slick-dots {
    bottom: 100px;
  }
}
@media (min-width: 1024px) {
  .slick-dots {
    bottom: 60px;
  }
}

