|------------------------------------*/
.l-footer--carousel {
  display: block;
  position: relative;
  bottom: 0;
  overflow: hidden;
}

.l-footer--middle {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #1d1d1d;
  color: #fff;
