.l-form input[type=email],
.l-form textarea {
  width: 100%;
  background-color: #f1d133;
  border: none;
  padding: 15px;
  border-radius: 5px;
}
.l-form input[type=text]::placeholder,
.l-form input[type=email]::placeholder,
.l-form textarea::placeholder {
  color: #000;
  font-family: "SD Georgia Light", serif;
  font-size: 16px;
