  background-color: transparent;
  font-weight: 700;
  font-family: "Open Sans", sans-serif;
  padding: 0;
}

.btn--search {
  border: none;
  border-radius: 5px;
  background-color: #ffdd35;
  font-weight: 700;
  font-family: "Open Sans", sans-serif;
  justify-content: center;
  font-size: 16px;
  font-size: 1rem;
  line-height: 20px;
  line-height: 1.25rem;
  letter-spacing: 0.2px;
  letter-spacing: 0.0125rem;
}

.btn--brg {
  cursor: pointer;
  border: none;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  text-align: center;
  width: 100px;
  height: 97px;
  z-index: 999999;
  text-align: center;
}
.btn--brg:active, .btn--brg:focus {
  outline: none;
}
.btn--brg span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #888;
  border-radius: 5px;
  opacity: 1;
  left: 0;
  right: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
  width: 34px;
  margin: 0 auto;
}
@media (max-width: 599px) {
  .btn--brg {
    width: 75px;
    height: 90px;
  }
}

.btn--brg span:nth-child(1) {
  top: 37px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.btn--brg span:nth-child(2) {
  top: 47px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.btn--brg span:nth-child(3) {
  top: 57px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.is-menu-expanded .btn--brg span:nth-child(1) {
  transform: rotate(45deg) translateY(-3px);
}
.is-menu-expanded .btn--brg span:nth-child(2) {
  width: 0%;
  opacity: 0;
}
.is-menu-expanded .btn--brg span:nth-child(3) {
  transform: rotate(-45deg) translateY(3px);
}

